import { ModalController, LoadingController } from "ionic-angular";
import { Component } from "@angular/core";
import { String } from "../../Model/String";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { FileImportAfterViewModel } from "../../Model/ViewModel/FileImportAfterViewModel";
import { GlobalComponent } from "../../components/global/global";

/**
 * Generated class for the FileImportAfterModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-file-import-After-add-edit-modal",
  templateUrl: "file-import-After-add-edit-modal.html"
})
export class FileImportAfterAddEditModalPage {
  item: FileImportAfterViewModel;
  mode: string;
  CanEditBatch: boolean;
  title: string;
  FileImportAfter_type: string;
  change_mode:string;
  file_type_key:string;
  sql_help:string=`
  變數說明：
  {batch_log_seq}=批次紀錄序號,
  {parent_batch_log_seq}=上層批次紀錄序號,
  {exec_log_seq}=轉檔序號,
  {job_step_log_seq}=執行作業步驟紀錄序號,
  {filename}=檔名,
  {schema}=結構描述,
  {tablename}=匯入資料表名稱,
  {ext_column}=異常關鍵字欄位,
  {src_path_file}=來源路徑檔名,
  {src_file_path}=來源檔案路徑,
  {bk_path_file}=備份路徑檔名,
  {bk_file_path}=備份檔案路徑,
  {file_size}=檔案大小,
  {file_ext}=附檔名,
  @bindarydata=二進位檔案內容,
  {JOB01},{JOB02},{JOB03}=作業迴圈參數,
  {LC01},{LC02},{LC03}=內部檔案迴圈參數,
  `;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    public global: GlobalComponent,
  ) {

    this.mode = navParams.data.mode;
    this.change_mode=navParams.data.change_mode;
    this.file_type_key=navParams.data.file_type_key;
    this.item = new FileImportAfterViewModel();
    this.item.exec_file_seq=navParams.data.item.exec_file_seq;
    this.item.exec_seq=navParams.data.item.exec_seq;
    this.item.import_sql_seq=navParams.data.item.import_sql_seq;
    if(this.mode==="POST")
    {
      this.item.exec_seq=navParams.data.totalRows+1;

      this.item.import_sql_seq=this.item.exec_file_seq*100+this.item.exec_seq;
    }
    this.item.schemaname=navParams.data.item.schemaname;
    this.item.tablename=navParams.data.item.tablename;
    this.item.db_conn_id=navParams.data.item.db_conn_id;
    this.item.parameter_column_group=navParams.data.item.parameter_column_group;
    this.item.is_active=navParams.data.item.is_active;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;
    this.item.sql_statement=navParams.data.item.sql_statement;
    this.item.prog_type_key=navParams.data.item.prog_type_key;
    this.item.prog_type=navParams.data.item.prog_type;
    this.item.program_no=navParams.data.item.program_no;
    this.item.program_desc=navParams.data.item.program_desc;
    this.item.sql_statement=navParams.data.item.sql_statement;

    this.CanEditBatch = navParams.data.CanEditBatch;

    this.title=String.Format("{0}.{1}", this.item.schemaname,this.item.tablename);
  }
  Help() {

    this.global.createLoader();

    this.global.loading.present().then(() => {
       this.global.showMessage("SQL語法說明",this.sql_help);

    });

  }
  SelectProgram() {
    let modal = this.modalCtrl.create("BatchProgramSelectModalPage", {
      select_item: this.item.program_no,
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
       this.item.program_no= select_data.program_no;
       this.item.program_desc=select_data.program_desc;
       this.item.sql_statement=select_data.sql_statement;
       this.item.is_active=select_data.is_active;
    });

    modal.present();
  }
  SelectProgType() {
    let modal = this.modalCtrl.create("CodeSelectModalPage", {
      select_key: this.item.prog_type_key,
      code_key: "066|00,066|01"
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.prog_type_key = select_data.code_key;
      this.item.prog_type = select_data.code_desc;
    });

    modal.present();
  }






  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad FileImportAfterModalPage");
  }
}



