export class FileImportAfterViewModel {
  /// <summary>
  ///  匯入SQL流水號
  /// <summary>
  public import_sql_seq: number;
  /// <summary>
  ///  轉檔序號
  /// <summary>
  public exec_file_seq: number;
  /// <summary>
  ///  結構描述
  /// <summary>
  public schemaname: string;
  /// <summary>
  ///  資料表
  /// <summary>
  public tablename: string;
  /// <summary>
  ///  db連線編號
  /// <summary>
  public db_conn_id: string;
  /// <summary>
  ///  參數欄位群組
  /// <summary>
  public parameter_column_group: string;
  /// <summary>
  ///  程式編號
  /// <summary>
  public program_no: string;
  /// <summary>
  ///  執行順序
  /// <summary>
  public exec_seq: number;
  /// <summary>
  ///  自訂SQL程式類別主鍵
  /// <summary>
  public prog_type_key: string;
  /// <summary>
  ///  啟用註記
  /// <summary>
  public is_active: boolean;
  /// <summary>
  ///  建立者
  /// <summary>
  public creator: string;
  /// <summary>
  ///  建立時間
  /// <summary>
  public create_time: Date;
  /// <summary>
  ///  最後更新者
  /// <summary>
  public modifier: string;
  /// <summary>
  ///  最後更新時間
  /// <summary>
  public last_update_time: Date;
  /// <summary>
  ///  程式說明
  /// <summary>
  public program_desc: string;
  /// <summary>
  ///  sql語法
  /// </summary>
  public sql_statement: string;
  /// <summary>
  ///  程式類別
  /// <summary>
  public prog_type: string;

  public is_active_color: string;
  public is_active_desc: string;
}



